
import "./index.scss"
import {Head} from "../../components/head"
import {ReactSwipe} from "../../components/swipe"
import { NoticeBar, WhiteSpace, Icon } from 'antd-mobile';
import {connect} from "react-redux";
import {getXhBookAjax} from "../../redux/actions"
import {getRxBookAjax} from "../../redux/actions"
import {getDsBookAjax} from "../../redux/actions"
    

    @connect(
        state=>{
            return{
                XhBook:state.XhBook,
                RxBook:state.RxBook,
                DsBook:state.DsBook
            }
               
            
        }
    )
export class Home extends Component{
    state = {
        banner:[
            require("../../../assets/images/6.jpg"),
            require("../../../assets/images/7.jpg"),
             require("../../../assets/images/8.jpg"),
             require("../../../assets/images/9.jpg"),
             require("../../../assets/images/10.jpg")
        ]
    }
   
    componentWillMount(){
      let {dispatch} = this.props;
      console.log(this.props)
    dispatch(getXhBookAjax({url:"/vue/getBookList",type:"",limit:3,cb(){console.log("success")}}))
    dispatch(getRxBookAjax({url:"/vue/getBookList",type:"",limit:3,cb(){console.log("success")}}))
    dispatch(getDsBookAjax({url:"/vue/getBookList",type:"",limit:3,cb(){console.log("success")}}))
    }
   gotoDetail(id){
    window.location.href="#/bookdetial?_id="+id

   }
    render(){
      
      
        let imgs = this.state.banner.map((b,i)=>{
            return (
                <img  onClick={()=>this.gotoApp(i)} key={i} src={b} alt="" className="img-item"/>
            )
        })
        
        return (
            <div>
                <Head title=""  {...this.props} ></Head>
                <WhiteSpace size="lg" />
    <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
      Notice: The arrival time of incomes and transfers of Yu &#39;E Bao will be delayed during National Day.
    </NoticeBar>
    <WhiteSpace size="lg" />
                <div id="box">
                <ReactSwipe id="banner" options={{loop:true,speed:1500,autoplay:true}}>
                    {imgs}
                </ReactSwipe>
            </div> 
           
            <section className="box newsbox moveIn">
            <div className="title">
          
            <a href="" title="" ref={(ref)=>this.a=ref}></a>
            </div>
            <ul>
            {this.props.XhBook.map((book,index) => (
                       <li key={index}>
                       <img
                            src={book.img}
                            alt=""
                          onClick={()=>this.gotoDetail(book._id)}
                           
                        />
                        <p>{book.name}</p>
                        </li>
                        
                      
                    ))}
              
            </ul>
            </section>
            <section className="box newsbox moveIn">
            <div className="title">
          
            <a href="" title="" id="rexue"></a>
            </div>
            <ul>
            {this.props.RxBook.map((book,index) => (
                       <li key={index}>
                       <img
                            src={book.img}
                            alt=""
                          
                            onClick={()=>this.gotoDetail(book._id)}
                             
                        />
                        <p>{book.name}</p>
                        </li>
                        
                      
                    ))}
            </ul>
            </section>
            <section className="bottoms box newsbox moveIn ">
            <div className="title">
          
            <a href="" title="" id="dushi"></a>
            </div>
            <ul>
            {this.props.DsBook.map((book,index) => (
                       <li key={index}>
                       <img
                            src={book.img}
                            alt=""
                          
                            onClick={()=>this.gotoDetail(book._id)}
                             
                        />
                        <p>{book.name}</p>
                        </li>
                        
                      
                    ))}
            </ul>
            </section>
            </div>
        )
    }
}


