import "./index.scss"
import {Head} from "../../components/head"
import { WhiteSpace, Toast } from 'antd-mobile';
import {connect} from "react-redux";
import { deleteShouCangBookAjax, getBookShouCangAjax } from "../../redux/actions";
import {observer} from "mobx-react";
import shoucang from "../../mobx/shoucang"
// import shujia from "../../mobx/shujia";

// @connect(
//     state=>{
//         return{
//             shoucang:state.shoucang
//         }
//     }
// )
@observer
export class Shoucang extends Component{

    componentWillMount(){
        if(localStorage.username){
        //     let {dispatch} = this.props;
        // dispatch(getBookShouCangAjax({url:"/vue/getBookShouCang",username:localStorage.username,cb(){console.log("success")}}))
            shoucang.getshoucang(localStorage.username)

        }else{
            this.props.history.push("/login")
        }
        
    }
    gotoDetail(id){
        window.location.href="#/bookdetial?_id="+id
    
       }
       deleteBookJia(_id){
        //    let {dispatch} = this.props;
        //    dispatch(deleteShouCangBookAjax({url:"/vue/deleteBookShouCangAjax",id:_id,cb(){console.log("success")}}))
        shoucang.deleteshoucangbook(_id)
       }
    render(){
        // const {
        //     shoucang
        // } = this.props;
       
        return (
            <div>
                <Head title="" show={true} {...this.props}></Head>
           <WhiteSpace></WhiteSpace>
           <div style={{display:  shoucang.shoucangbook.length>0?'none':'block'}}>
           <p></p>
           </div>
            <ul className="shuajia-ul" style={{display:  shoucang.shoucangbook.length>0?'block':'none'}}>
                {
                   shoucang.shoucangbook.map((book,index)=>{
                       return(
                           <li key={index}>
                           <div>
                           <img src={book.img}   onClick={()=>this.gotoDetail(book.bookid)}/>
                           </div>
                       
                           <p>{book.bookname}</p>
                           <p className="delete" onClick={()=>this.deleteBookJia(book._id,index)}></p>
                          
                           </li>
                       )
                    })
                }
               
            </ul>
          
            </div>
        )
    }
}


