import { types } from "util";

//StoreActionState,view,

//Reducer,ActionState State

//Redurce,.


const defaultState = {
    XhBook:[],
    RxBook:[],
    DsBook:[],
    types:[],
    book:[],
    detailBook:{},
    comments:[],
    msg:"",
    Booklist:[],
    msg:[],
    bookjia:[],
    count:"",
    bookjiaNum:"",
    shoucang:[],
    shoucangNum:"",
    totalPiao:{}


}
export const reducers = (state=defaultState,action)=>{
    console.log(action);
    switch(action.type){

    case "getXhBook": 
    return {...state,...{XhBook:action.XhBook}}
    break;

    case "getRxBook": 
    return {...state,...{RxBook:action.RxBook}}
    break;

    case "getDsBook": 
    return {...state,...{DsBook:action.DsBook}}
    break;

    case "getTypes": 
    return {...state,...{types:action.types}}
    break;

    case "getSearchBookAjax": 
    return {...state,...{book:action.book}}
    break;

    case "getDetailBookAjax": 
    return {...state,...{detailBook:action.detailBook}}
    break;

    case "getBookPlAjax": 
    return {...state,...{comments:action.comments}}
    break;

    case "addBookPlAjax": 
    return {...state,...{comments:action.comments}}
    break;

    case "deleteBookPlAjax": 
    return {...state,...{msg:action.msg}}
    break;

    case "getBookListAjax": 
    return {...state,...{Booklist:action.Booklist}}
    break;

    case "addBookJiaAjax": 
               
    return {...state,...{msg:action.msg}}
    break;

   

    case "getBookJiaAjax": 
               
    return {...state,...{bookjia:action.bookjia}}
    break;

   

    case "getCommentNumAjax": 
               
    return {...state,...{count:action.count}}
    break;

    case "getBookJiaNumAjax": 
               
    return {...state,...{bookjiaNum:action.bookjiaNum}}
    break;

    case "deleteBookJiaAjax": 
               
    return {...state,...{msg:action.msg}}
    break;

    case "shouCangBookAjax": 
               
    return {...state,...{msg:action.msg}}
    break;

    case "getBookShouCangAjax": 
               
    return {...state,...{shoucang:action.shoucang}}
    break;

    case "getShouCangNumAjax": 
               
    return {...state,...{shoucangNum:action.shoucangNum}}
    break;

    case "deleteShouCangBookAjax": 
               
    return {...state,...{msg:action.msg}}
    break;

    case "touPiaoAjax": 
               
    return {...state,...{msg:action.msg}}
    break;

    case "getBookPiaoAjax": 
               
    return {...state,...{totalPiao:action.totalPiao}}
    break;

    
    case "getBookPiaoAjax": 
               
    return {...state,...{totalPiao:action.totalPiao}}
    break;

    case "BookSearch": 
    return {...state,...{book:action.book}}
    break;
    
        default:
        return state
        break;
    }
}


