import "./index.scss";

import {NavLink,Link,} from "react-router-dom"

import { TabBar,PullToRefresh } from 'antd-mobile';

import {connect} from "react-redux";
// @connect()
export class BookList extends Component{

    state = {
        refreshing:false
    }
    gotoDetail(id){
        window.location.href="#/bookdetial?_id="+id
    
       }
render(){
    let {
       bookslist,
       dispatch,
    
    } = this.props
    console.log(bookslist)
    return(
        <div>
            <ul className="booklist">
            <PullToRefresh
                    damping={60}
                    ref="loadmore"
                    style={{
                    
                        overflow: 'auto',
                    }}
                    indicator={'' }
                    direction={'down'}
                    refreshing={this.state.refreshing}
                    onRefresh={() => {
                        this.setState({ refreshing: true });
                      
                        setTimeout(() => {
                            var booklist = bookslist.reverse();
                            dispatch( {
                                type:"getBookListAjax",
                                booklist
                            })
                            this.setState({ refreshing: false });
                        }, 1000);
                    }}
                >
            {bookslist.map((book,index) => (
                       <li key={index} className="moveIn">
                       <img
                            src={book.img}
                            alt=""
                          onClick={()=>this.gotoDetail(book._id)}
                           
                        />
                        <p>{book.name}</p>
                        </li>
                        
                      
                    ))}
               </PullToRefresh>
            </ul>
        </div>
    )   
}
}


