
import "./index.scss"
import {Head} from "../../components/head"




import {connect} from "react-redux"
import { getDetailBookAjax } from "../../redux/actions";

 @connect(
     state=>{
                
         return {
            detailBook:state.detailBook,
                    }
                }
            )
 export  class Readbook extends Component{
    componentWillMount(){
        let {dispatch} = this.props
      var _id =  this.props.location.search.split("?")[1].split("=")[1]
     
      dispatch(getDetailBookAjax({url:"/vue/getBookDetail",id:_id,cb(){console.log("success")}}))
    
    }
            
     render(){
         const {
          detailBook
          } = this.props
      console.log(detailBook+"66666")
         return (
          <div>
             <Head title={detailBook.name} show={true} {...this.props} ></Head>
            
                  <div className="readbook">
                  <div className="img">
                      <img src={detailBook.img} />    
                     <p className="shuming">{detailBook.name}</p> 
                     <p className="user">{detailBook.user} </p>             
                  </div>
                <div className="xiangqing">
                    <ul>
                        <li>
                            <p className="detatlbook">{detailBook.type}</p>
                            <p></p>
                        </li>
                        <li>
                        <p className="detatlbook">2019-04-15</p>
                        <p></p>
                        </li>
                        <li>
                        <p className="detatlbook">{detailBook.view}</p>
                        <p></p>
                        </li>
                    </ul>
                </div>
                <div className="neirong">
                <h1> AR</h1>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                </div>
                  </div>
            
                       
              </div>
                    )
                }
            }


