
import "./index.scss"
import {Head} from "../../components/head"
import {connect} from "react-redux";
import {getTypesAjax,getBookListAjax} from "../../redux/actions"
import { Tabs, WhiteSpace, Badge } from 'antd-mobile';
import {BookList} from "../../components/booklist"

@connect(
    state=>{
        return{
            types:state.types,
            booklist:state.Booklist
        }
    }
)

export class Classify extends Component{

    componentWillMount(){
        let {dispatch} = this.props;
        console.log(this.props)
        dispatch(getTypesAjax({url:"/vue/getBooksTypes",cb(){console.log("success")}}))
        dispatch(getBookListAjax({url:"/vue/getBooks",cb(){console.log("success")}}))
      
    }
    classify(tab){
        console.log(tab)
    }
    render(){
        const {
            types,
            booklist
        } = this.props;
        console.log(this.props);
        const tabs = types.map((item,i)=>{
            var obj = {}
            obj.title  = item;
        // item.title = item
            return obj;
        })
        console.log(tabs)
        return (
            <div>
                <Head title=""  {...this.props} ></Head>
                <Tabs tabs={tabs}
                    initialPage={0}
                   
                >
               
                    {
                        tabs.map((type,i)=>{
                            console.log(type)
                            return (
                               <BookList key={i}
                               books={booklist}
                               bookslist = {booklist.filter((book)=>book.type==type.title)}
                               
                               ></BookList>
                            
                            
                            )
                        })
                    }
                </Tabs>
            </div>
        )
    }
}


