
import "./index.scss";

import { NavLink } from "react-router-dom"

import { TabBar } from 'antd-mobile';
import {connect} from "react-redux";
import { getBookJiaNumAjax } from "../../redux/actions";
@connect(
    state=>{
        return{
            bookjiaNum:state.bookjiaNum
        }
    }
)

export class Foot extends Component {

    state = {
        foots: [
            { txt: "", path: "/app/home", name: "home", icon: "icon-home", imgs: { on: require("../../../assets/images/home2.png"), off: require("../../../assets/images/home1.png") } },
            { txt: "", path: "/app/classify", name: "classify", icon: "icon-goodsfill", imgs: { on: require("../../../assets/images/classify2.png"), off: require("../../../assets/images/classify1.png") } },
            { txt: "", path: "/app/shopcar", name: "car", icon: "icon-shop_car", imgs: { on: require("../../../assets/images/shujia2.png"), off: require("../../../assets/images/shujia1.png") } },
            { txt: "", path: "/app/mine", name: "mine", icon: "icon-my", imgs: { on: require("../../../assets/images/mine2.png"), off: require("../../../assets/images/mine1.png") } },
        ],
      
        selectedTab: "home"
    }

    componentWillMount() {
        let username = localStorage.username;
        console.log(this.props);
        var pathname = this.props.location.pathname;
        var tab = pathname.split("/app/")[1];

        console.log(tab);
        this.setState({
            selectedTab: tab
        })
        let {dispatch} = this.props;
        dispatch(getBookJiaNumAjax({url:"/vue/getBookJiatNum",user:username,cb(){console.log("success")}}))
    }

    render() {
        const {
            bookjiaNum
        } = this.props;
        return (
            <div className="foot">
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="#33A3F4"
                    barTintColor="white"
                >
                    {
                        this.state.foots.map((foot, i) => {
                            return (
                                <TabBar.Item
                                    title={foot.txt}
                                    key={i}
                                    icon={
                                        <img style={{width:22,height:22}} src={foot.imgs.off}/>
                                    }
                                    selectedIcon={
                                        <img style={{width:22,height:22}} src={foot.imgs.on}/>
                                    }
                                   
                                    selected={this.state.selectedTab === foot.name}
                                    badge={i == 2 ? bookjiaNum : 0}
                                    onPress={() => {
                                        this.setState({
                                            selectedTab: foot.name,
                                        });
                                        this.props.history.push(foot.path);
                                    }}
                                    data-seed="logId"
                                >

                                </TabBar.Item>
                            )
                        })
                    }

                </TabBar>
            </div>
        )
    }
}


