// export const http = axios;
export const http = {
    /** get 
     * @param  {} url
     * @param  {} params
     */
    get(url,params){
        return new Promise(function(resolve,reject){
            axios.get(url,{
                params:params
            })
            .then(res=>{
                resolve(res.data);
            })
            .catch(error=>{
                reject(error);
            })
        })
    },
    /** post 
     * @param  {} url
     * @param  {} body
     * @param  {} params
     */
    post(url,body,params){
        return new Promise(function(resolve,reject){
            axios.post(url,
                body,
                {
                    params
                }
            ).then(res=>{
                resolve(res.data);
            })
            .catch(error=>{
                reject(error)
            })
        })
    }
}

