



import "./index.scss"
import {Head} from "../../components/head"
import {InputItem,List,WingBlank,WhiteSpace,Button} from "antd-mobile";
import {http} from "../../utils"
var timer = null;
export class Login extends Component{

    state = {
        txt:"",
        count:60,
        disabled:true,
        telFlag:true
    }

    calcTime=()=>{
        if(!timer){
            timer = setInterval(()=>{
                if(this.state.count>0){
                    this.state.count--;
                    this.setState({
                        txt: this.state.count + ' s',
                        disabled:true
                    })
                }else{
                    clearInterval(timer);
                    timer = null;
                    this.setState({
                        txt:"",
                        count:60,
                        disabled:false
                    })
                }
            },1000)
        }
    }   

    getCode=()=>{
        
        http.post("/vue/sendCode",{
            mobile:this.refs.mobile.state.value
        }).then(res=>{
            this.calcTime();
        })
    }

    checkTel=(val)=>{
        
        var telReg = /^1(3|5|7|8|9)\d{9}$/
        
        if(telReg.test(val)){
            this.setState({
                disabled:false
            })
        }else{
            this.setState({
                disabled:true
            })
        }
    }

    checkCode=(val)=>{
        var codeReg = /^\d{4}$/
        
        if(codeReg.test(val)){
            this.setState({
                telFlag:false
            })
        }else{
            this.setState({
                telFlag:true
            })
        }
    }

    startLogin=()=>{
        http.post("/vue/testCode",{
            mobile:this.refs.mobile.state.value,
            code:this.refs.code.state.value
        }).then(res=>{
         if(res.type=="1"){
             localStorage.username = this.refs.mobile.state.value;
             history.go(-1);
         }
        })
    }

    

    render(){
        const {txt,disabled,telFlag} = this.state;
        return (
            <div>
               <Head title=""  show={true} {...this.props}></Head>
               <WingBlank>
                <List>  
                        <WhiteSpace/>
                        <InputItem
                            onChange={this.checkTel}
                            type="tel"
                            placeholder=""
                            ref="mobile"
                        ></InputItem>
                        <WhiteSpace/>
                        <div className="big">
                        <div className="yzm">
                        <InputItem
                            type="text"
                            placeholder=""
                            ref="code"
                            onChange={this.checkCode}
                        ></InputItem>
                        </div>
                        <Button disabled={disabled} onClick={this.getCode} inline type="warning">{txt}</Button>
                        </div>
                      
                       
                    </List>
               </WingBlank>
               <button className="login" onClick={this.startLogin}  disabled={telFlag} ></button>
            </div>
        )
    }
}


