import { observable, action } from "mobx";
import { http } from "../utils";
import Item from "_antd@3.16.5@antd/lib/list/Item";



class BookJia{
    constructor(){

    }


@action getbookjia=(username)=>{
    http.get("/vue/getBookJia",{
        username
    }).then(res=>{
        this.bookjia = res.result;
        console.log(this.bookjia)
    })
}
@action deletebookjia=(_id)=>{
    http.get("/vue/deleteBookJiaAjax",{
        _id
    }).then(res=>{
       this.bookjia = this.bookjia.filter((item)=>item._id!=_id)
    })
}

@action getBookAll = (limit)=>{
    http.get("/vue/getBooks",{
        limit
    }).then(res=>{
        this.bookAll = res.result;
        console.log(this.bookAll)
    })
}
@action getyouxi = ()=>{
    http.get("/vue/getyouxi",{})
    .then(res=>{
        this.youxi = res.result
    })
}
@action addGoods = (goodsid,goodsimg,goodsname,goodsprice,username) =>{
    http.get("/vue/shoppingcar",{
        goodsid,goodsimg,goodsname,goodsprice,username
    }).then(res=>{
        this.msg = res.result.msg
    })
}

    @observable msg = "";
    @observable youxi = [];
    @observable bookAll =[];
    @observable bookjia = [];

}

export default new BookJia();












