

//  

import {HashRouter as Router , BrowserRouter,Switch,Route,Redirect } from "react-router-dom";
import { Guide } from "./guide";
import { App } from "./app";
import { Search } from "./search";
import { BookDetial } from "./bookdetial";
import { Login } from "./login";
import { Readbook } from "./readbook";
import { Shoucang } from "./shoucang";
import { Shopcar } from "./shopcar";
import { ShoppinngCar } from "./shoppingcar";




export class IndexView extends Component {
    render(){
        return (
            <div className="main">
                <Router basename="/">
                <Switch>
                    <Route path="/" exact render={()=><Redirect to="/guide" />}></Route>
                    <Route path="/guide" component={Guide}></Route>
                    <Route path="/app/" script component={App}></Route>
                    <Route path="/search" script component={Search}></Route>
                    <Route path="/login" script component={Login}></Route>
                    <Route path="/bookdetial" script component={BookDetial}></Route>
                    <Route path="/shoucang" script component={Shoucang}></Route>
                    <Route path="/shujia" script component={Shopcar}></Route>
                    <Route path="/readbook" script component={Readbook}></Route>
                    <Route path="/shoppingcar" script component={ShoppinngCar}></Route>
                </Switch>
                </Router>
            </div>
        )
    }
}

 


