

import "./index.scss"
import {Head} from "../../components/head"
import {connect} from "react-redux";
import { SearchBar, Button, WhiteSpace, WingBlank } from 'antd-mobile';
import { getSearchBookAjax, BookSearch } from "../../redux/actions";


@connect(
    state=>{
        return{
            book:state.book
        }
    }
)
export class Search extends Component{



    componentWillMount(){
        let {dispatch} = this.props;
        dispatch(BookSearch())
    }
    getresult=(val)=>{
        console.log(val)
        let {dispatch} = this.props;
        dispatch(getSearchBookAjax({url:"/vue/search",keyword:val,cb(){console.log("succsess")}}))
    }
    gotoDetail(id){
        window.location.href="#/bookdetial?_id="+id
    
       }
    render(){
        return (
            <div>
                <Head title="" show={true} {...this.props} ></Head>
                <WingBlank></WingBlank>
                 <SearchBar placeholder="Search" onSubmit={this.getresult} maxLength={8} />
                 <WhiteSpace />
                 <section className="box newsbox moveIn">
           
            <ul>
            {this.props.book.map((book,index) => (
                       <li key={index}>
                       <img
                            src={book.img}
                            alt=""
                        onClick={()=>this.gotoDetail(book._id)}
                           
                        />
                        <p>{book.name}</p>
                        </li>
                        
                      
                    ))}
              
            </ul>
            </section>
            </div>
        )
    }
}


