import "./index.scss"
// import {Head} from "../../components/head"
import {HashRouter as Router , BrowserRouter,Switch,Route,Redirect } from "react-router-dom";
import {Home} from "../home"
import {Classify} from "../classify";
import {Shopcar} from "../shopcar";
import {Mine} from "../mine";
import {Foot} from "../../components/foot"
export class App extends Component{
    render(){
        return (
            <div id="box">
              <Switch>
                  <Route path="/app/home" component={Home}></Route>
                  <Route path="/app/classify" component={Classify}></Route>
                  <Route path="/app/shopcar" component={Shopcar}></Route>
                  <Route path="/app/mine" component={Mine}></Route>
                  {/* <Route component={Home}></Route> */}
                  <Route render={()=><Redirect to="/app/home"></Redirect>}></Route>
              </Switch>
              <Foot {...this.props} location={this.props.location}></Foot>
            </div>
        )
    }
}


