import { observable, action } from "mobx";
import { http } from "../utils";

class shoppingcar{
    constructor(){

    }

@action getGoodsList=(username)=>{
 http.get("/vue/getgoodscar",{
    username
 }).then(res=>{
     this.goodsList = res.result;
     this.carNum = 0;
     this.goodsList.forEach((item)=>{
         this.carNum+=item.goodsnum
     })
 })
}

@action changeOne = (checked,goodsid)=>{
    this.goodsList = this.goodsList.map((item)=>{
        if(item.goodsid==goodsid){
            item.checked = checked;
        }
        return item
    })
    this.watchToal()
    this.watchQuan()
    
}
@action watchToal = ()=>{
    this.total = 0;
    this.goodsList.forEach((item)=>{
        if(item.checked){
            this.total +=item.goodsprice*item.goodsnum*1
        }
        return item
    })
}

@action watchQuan=()=>{
    var flag = true;
    this.goodsList.forEach((item)=>{
        if(!item.checked){
           flag = false
        }
        this.quan = flag
    })
   
}
@action changeQuan=(checked)=>{
    this.quan = checked
    this.goodsList = this.goodsList.map((item)=>{
        item.checked = checked;
        return item;
    })
    this.watchToal()
}
@action changeOneCount = (goodsid,flag)=>{
    this.goodsList = this.goodsList.map((item)=>{
        if(item.goodsid==goodsid){
            item.goodsnum+=flag?-1:1;
        }
        return item;
    })
    this.watchToal()
}
@action setOneGoodsnum=(goodsid,goodsnum)=>{
    this.goodsList = this.goodsList.map((item)=>{
        if(item.goosid==goodsid){
            item.goodsnum = goodsnum
        }
        return item
    })
    this.watchToal()
}
@action deleteChecked = ()=>{
    var goodids = [];
    this.goodsList.forEach((item)=>{
        if(item.checked){
            goodids.push(item.goodsid)
        }
    })
    http.get("/vue/deleteGoodsBooks",{
        username:localStorage.username,
        goodids
    }).then(res=>{
        console.log(res)
    })
    this.goodsList = this.goodsList.filter(item=>!item.checked)
    this.watchToal();
    this.watchQuan()
}

    @observable quan = false;
    @observable goodsList = [];
    @observable total = 0;
    @observable carNum = 0;

}

export default new shoppingcar();


