
import "./index.scss"
// import {Head} from "../../components/head"
import {ReactSwipe} from "../../components/swipe"

export class Guide extends Component{
   state = {
       banner:[
           require("../../../assets/images/1.jpg"),
           require("../../../assets/images/2.jpg"),
            require("../../../assets/images/3.jpg"),
            require("../../../assets/images/4.jpg"),
            require("../../../assets/images/5.jpg")
       ]
   }
   gotoApp=(index)=>{
    if(index==this.state.banner.length-1){
        this.props.history.push("/app/")
    }
   }
   componentWillMount(){
       if(localStorage.reactCount){
           localStorage.reactCount+=1;
           if(localStorage.reactCount>3){
               this.props.history.push("/app/")
           }
       }else{
        localStorage.reactCount=1;
       }
   }
    render(){
        let imgs = this.state.banner.map((b,i)=>{
            return (
                <img  onClick={()=>this.gotoApp(i)} key={i} src={b} alt="" className="img-item"/>
            )
        })
        return (
            <div id="box">
                <ReactSwipe id="banner" options={{loop:true,speed:1500,autoplay:true}}>
                    {imgs}
                </ReactSwipe>
            </div>
        )
    }
}


