import {http} from "../../utils"
//
export const getXhBookAjax = ({url,type,limit,cb})=>{
    return http.get(url,{
        type,
        limit
       
    }).then(res=>{
        cb();
        return{
            type:"getXhBook",
            XhBook:res.result
        }
    })
}
