//
export const deleteBookPlAjax = ({url,id,user,cb})=>{
    return http.get(url,{
         id,
        user
       
    }).then(res=>{
        if(res.type=="1"){
            console.log(res)
        cb();
        return {
            type:"deleteBookPlAjax",
             msg:res.result.msg
         }
        }else{
            Toast.offline('!!!', 1);  
        }
       
    })
}
