
import "./index.scss"
import {Head} from "../../components/head"
import { Carousel } from 'antd-mobile';
import { WhiteSpace, Toast } from 'antd-mobile';
import {connect} from "react-redux";
import { getBookJiaAjax,deleteBookJiaAjax } from "../../redux/actions";
import shujia from "../../mobx/shujia";
import {observer} from "mobx-react"

import { Tabs, Badge } from 'antd-mobile';


// @connect(
//     state=>{
//         return{
//             bookjia:state.bookjia
//         }
//     }
// )
@observer
export class Shopcar extends Component{

  
        state = {
            banner:[
                require("../../../assets/images/youxi1.jpg"),
                require("../../../assets/images/youxi2.jpg"),
                 require("../../../assets/images/youxi3.jpg"),
                 require("../../../assets/images/youxi4.jpg"),
                 require("../../../assets/images/youxi5.jpg")
            ]
     
    }
    componentWillMount(){
        if(localStorage.username){
         
            shujia.getbookjia(localStorage.username)
            shujia.getBookAll(4)
            shujia.getyouxi()

        }else{
            this.props.history.replace("/login")
        }
        
    }
    gotoDetail(id){
        window.location.href="#/bookdetial?_id="+id
    
       }
       deleteBookJia(_id){
       
        shujia.deletebookjia(_id)
       }
      gotoclassify=()=>{
         this.props.history.push("/app/classify")
      }
      gotocar(goodsid,goodsimg,goodsname,goodsprice,username){
        shujia.addGoods(goodsid,goodsimg,goodsname,goodsprice,username)
      }
    render(){
      
 console.log(this.state.banner)
       console.log(shujia.bookjia);
       const tabs = [
        { title: <Badge></Badge> },
        { title: <Badge></Badge> }
      
      ];

        return (
            <div>
                <Head title="" show={true} {...this.props}></Head>
        
           <div className="tupian" style={{display:shujia.bookjia.length>0?'none':'block'}}>
            
            
             <Tabs tabs={tabs}
      initialPage={0}
     
    >
      <div className="book" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center',  backgroundColor: '#fff' }}>
         <ul>
             {
                 shujia.bookAll.map((book,index)=>{
                     return(
                         <li key={index} className="moveIn">
                            <img
                            src={book.img}
                            alt=""
                          onClick={()=>this.gotoDetail(book._id)}
                           
                        />
                        <p>{book.name}</p>
                         </li>
                     )
                 })
             }
         </ul>
      </div>
      <div style={{   backgroundColor: '#fff' }}>
      <Carousel
          autoplay={true}
          infinite
        
        >
        {
            this.state.banner.map((img,index)=>{
               return(
                <img
                src={img}
                className="youxitu"
                key={index}
                style={{ height:"100%" , verticalAlign: 'top',textAlign:'center' }}
              
              />
               )
               
            })
        }
         
        </Carousel>
        <ul className="youxi-ul">
        {
            shujia.youxi.map((item,index)=>{
                return(
                    <li key={index}>
                    <div className="left">
                        <img src={item.img} />
                    </div>
                    <div className="right">

                    <p className="youxiname">{item.name}</p>
                    <p className="price">:{item.price}</p>
                    <p className="goumai" onClick={()=>this.gotocar(item._id,item.img,item.name,item.price,localStorage.username)}></p>
                    </div>
                   
                    </li>
                )
            })
        }
        </ul>
      </div>
     
    </Tabs>
            
           </div>
            <ul className="shuajia-ul" style={{display:shujia.bookjia.length>0?'block':'none'}}>
                {
                    shujia.bookjia.map((book,index)=>{
                       return(
                           <li key={index}>
                           <div>
                           <img src={book.img}   onClick={()=>this.gotoDetail(book.bookid)}/>
                           </div>
                       
                           <p>{book.bookname}</p>
                           <p className="delete" onClick={()=>this.deleteBookJia(book._id)}></p>
                          
                           </li>
                       )
                    })
                }
               
            </ul>
          
            </div>
        )
    }
}


