import "./index.scss"
import {Head} from "../../components/head"
import {connect} from "react-redux"
import { getDetailBookAjax, getBookPlAjax, addBookPlAjax, deleteBookPlAjax, addBookJiaAjax, getCommentNumAjax, shouCangBookAjax, touPiaoAjax, getBookPiaoAjax } from "../../redux/actions";
import { Tabs, WhiteSpace, Badge } from 'antd-mobile';
import { Button} from 'antd-mobile';
import { Toast } from 'antd-mobile';
var time = null;

@connect(
    state=>{
        return{
            detailBook:state.detailBook,
            comments:state.comments,
            msg:state.msg,
            count:state.count,
            totalPiao:state.totalPiao
        }
    }
)
export class BookDetial extends Component{
   
    state = {
        txt:"",
        h:23,
        m:59,
        s:59,
        disabled:false,
        color:"#d81616"
       
    } 

    componentWillMount(){
        let {dispatch} = this.props
      var _id =  this.props.location.search.split("?")[1].split("=")[1]
     
      dispatch(getDetailBookAjax({url:"/vue/getBookDetail",id:_id,cb(){console.log("success")}}))
      dispatch(getBookPlAjax({url:"/vue/getComments",id:_id,cb(){console.log("success")}}))
      dispatch(getCommentNumAjax({url:"/vue/getCommentNum",id:_id,cb(){console.log("success")}}))
      dispatch(getBookPiaoAjax({url:"/vue/getBookPiaoCount",id:_id,cb(){console.log("success")}}))
    }
    commentsAdd=()=>{
        if(localStorage.username){
            var neirong = this.refs.nr.value;
            var date = new Date();
            var year = date.getFullYear();
            var monnth = date.getMonth()+1;
            var day = date.getDate();
            var hour = date.getHours();
            var min = date.getMinutes();
            var sec = date.getSeconds();
            var time = year + "-" + monnth + "-" +day +"  "+ hour + ":" +min+":" +sec;
            var user =localStorage.username ;
            var _id =  this.props.location.search.split("?")[1].split("=")[1]
            if(neirong!=""){
                this.props.comments.unshift({
                    neirong,
                    time,
                    user
                })
                let {dispatch} = this.props;
                dispatch(addBookPlAjax({url:"/vue/addComment",neirong,time,user,bookid:_id,cb(){console.log("success")}}))
                // dispatch(updateComments(this.props.comments))
              
                this.refs.nr.value=  "";
            }else{
                Toast.offline('!!!', 1);
                
            }
        }else{
            this.props.history.push("/login")
        }
      
       
       
    }
    deleteComment(index,_id){
        var username = localStorage.username;
        if(username){
            var id = "item"+index;
            var ele = document.getElementById(id);
            ele.className = "";
            ele.className = "moveOut";
          
            setTimeout(()=>{
                this.props.comments.splice(index,1);
               
                this.props.comments.forEach((c,i)=>{
                    document.getElementById('item'+i).className = "";
                })
    
            },1000)
            let {dispatch} = this.props;
            dispatch(deleteBookPlAjax({url:"/vue/delComment",id:_id,user:username,cb(){console.log("success")}}))
        }else{

        }
     
    }
    //
    addBookJia(id,img,name,user){
        if(localStorage.username){
            let {dispatch} = this.props;
            setTimeout(() => {
            dispatch(addBookJiaAjax({url:"/vue/addBookJia",bookid:id,username:user,img,bookname:name,cb(){console.log("success")}}))
        }, 1500);  
        }else{
            this.props.history.push("/login")
        }
           
    }
    //
    shoucang(id,img,name,user){
        if(localStorage.username){
        let {dispatch} = this.props;
        setTimeout(() => {
        dispatch(shouCangBookAjax({url:"/vue/shouCangBook",bookid:id,username:user,img,bookname:name,cb(){console.log("success")}}))
    }, 1500);  
     }else{
     this.props.history.push("/login")
    }    
     }
    //
     toupiao(id,user){

         if(localStorage.username){

            let {dispatch} = this.props;
            dispatch(touPiaoAjax({url:"/vue/toupiao",id,piaonum:1,user,time:new Date()}))
           
                time = setInterval(()=>{
                    --this.state.s;
                    if(this.state.s<0){
                        --this.state.m;
                        this.state.s=59;
                    }
                    if(this.state.m<0){
                        --this.state.h;
                        this.state.s=m=59
                    }
                    if(this.state.h<0){
                        this.state.s=0;
                        this.state.m=0;
                    }
                    this.setState({
                        txt:this.state.h +":"+this.state.m +":" +this.state.s,
                        disabled:true,
                        color:"gray"
                    })
                  
                },1000)
               
            
         }else{
            this.props.history.push("/login")
         }
     }
    //
    readbook(_id){
        window.location.href="#/readbook?_id="+_id
    }
    render(){
        const {txt,disabled,color} = this.state;
        const {
            detailBook,
            msg,
            count,
            totalPiao
        } = this.props
        const {
            piaonum
        } = totalPiao||"0";
        const tabs = [
            { title: <Badge></Badge> },
            { title: <Badge text={count}></Badge> },
          
          ];
        return (
          
            <div>
                <Head title={detailBook.name} show={true}  {...this.props} ></Head>
               <div className="head">
               <div className="img">
               <img src = {detailBook.img} />
               </div>
               <div className="neirong">
               <h2>{detailBook.name}</h2>
               <p>{detailBook.type}</p>
               <p>{detailBook.user}</p>
               <p>{detailBook.zhuangtai}</p>
               <p>{detailBook.view}</p>
               <p className="tuijianpiao" style={{display:piaonum?'block':'none'}}>{piaonum}</p>
               </div>
            
               </div>
               <WhiteSpace>
              
               </WhiteSpace>
               <div className="footer">
   <button className="mfsd" onClick={()=>this.readbook(detailBook._id)}></button>
   <button className="jrsj" onClick={()=>this.addBookJia(detailBook._id,detailBook.img,detailBook.name,localStorage.username,msg.type)}></button>
   <button className="scgs" onClick={()=>this.shoucang(detailBook._id,detailBook.img,detailBook.name,localStorage.username,msg.type)}></button>
   <button className="ttjp" style={{backgroundColor:color}} disabled={disabled} onClick={()=>this.toupiao(detailBook._id,localStorage.username)}>{txt}</button>
   
    </div>
    <WhiteSpace>
              
              </WhiteSpace>  
               <Tabs tabs={tabs}
      initialPage={1}
      onChange={(tab, index) => { console.log('onChange', index, tab); }}
      onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
    >
      <div className="jianjie" style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
       <p>{detailBook.jianjie}</p>
      </div>
      <div style={{ display: 'flex',height:285, backgroundColor: '#fff',position:"relative" }}>
      <div className="zwpl" style={{display:this.props.comments.length>0?'none':'block'}}>
    <p>   </p>
      </div>
       <div className="qlxq" style={{display:this.props.comments.length>0?'block':'none'}}>
       <h2></h2>
      <ul className="pllb ">
      {
          this.props.comments.map((item,index)=>{
              return(
                <li id={"item"+index} key={index} className="moveIn">
                <div className="left"> <span className="name">{item.user}</span>  
                <p>{item.neirong}</p>
                <span className="time">{item.time}</span></div>
               <div className="right">
                   <p onClick={()=>{this.deleteComment(index,item._id)}}>x</p>
               </div>
            
            </li>
       
              )
          })
      }
         
          
      </ul>
       </div>
       <div className="fbpl">
     <input placeholder="" ref="nr"  className="shurukuang"/>
    <button className="btn" onClick={this.commentsAdd}></button>
       </div>
      </div>
     
    </Tabs>
       
            </div>
        )
    }
}


