

import { observable, action } from "mobx";
import { http } from "../utils";
import Item from "_antd@3.16.5@antd/lib/list/Item";



class shoucang{
    constructor(){

    }


@action getshoucang=(username)=>{
    http.get("/vue/getBookShouCang",{
        username
    }).then(res=>{
        this.shoucangbook = res.result;
        console.log(this.shoucangbook)
    })
}
@action deleteshoucangbook=(id)=>{
    http.get("/vue/deleteBookShouCangAjax",{
        id
    }).then(res=>{
       this.shoucangbook = this.shoucangbook.filter((item)=>item._id!=id)
    })
}






    @observable shoucangbook = [];

}

export default new shoucang();


