

import "./index.scss"
import {Head} from "../../components/head"
import { Button, NoticeBar, Checkbox } from "antd-mobile";

import {observer} from "mobx-react"
import shoppingcar from "../../mobx/shoppingcar";
@observer
export class ShoppinngCar extends Component{


    componentWillMount(){
        shoppingcar.getGoodsList(localStorage.username)
    }
    checkOne =e=>{
        console.log(e)
        shoppingcar.changeOne(e.target.checked,e.target.dataId)
    }
    checkAll=e=>{
        shoppingcar.changeQuan(e.target.checked)
    }
    reduce=(goodsid,goodsnum)=>{
        if(goodsnum>1){
            shoppingcar.changeOneCount(goodsid,true)
        }
        console.log(goodsid,goodsnum)
       
    }
    add=(goodsid,goodsnum)=>{

        shoppingcar.changeOneCount(goodsid,false)
    }
    changeCount=(goodsid,v)=>{
        console.log(v.target.value);
        if(v.target.value>1){
            shoppingcar.setOneGoodsnum(goodsid,v.target.value)
        }
    }
    delSelect=()=>{
        shoppingcar.deleteChecked()
    }
    render() {
        
        return(
            <div>
    <Head title="" show={true} {...this.props}></Head>
    
                    <div style={{ display: shoppingcar.goodsList.length > 0 ? 'none' : 'block' }}>
                        <h2>   ,  </h2>
                    </div> 
                    <div style={{ display: !shoppingcar.goodsList.length > 0 ? 'none' : 'block' }}>
                        <div id="carDiv">
                            <div id="tbody">
                                <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
                                    -01.26,
                            </NoticeBar>
                                {
                                   shoppingcar.goodsList && shoppingcar.goodsList.map((i, index) => {
                                       console.log(i.goodsid)
                                        return (
                                            <ul className="tr" key={index} id={i.goodsid}>
                                                <li style={{ width: "11%" }}>
                                                    <Checkbox checked={i.checked} dataId={i.goodsid} onChange={this.checkOne} >
                                                    </Checkbox>
                                                </li>
                                                <li style={{ width: "24%" }}><img src={i.goodsimg} /></li>
                                                <li style={{ width: "38%" }}>
                                                    <p style={{ lineHeight: "18px", marginTop: '36px' }}>{i.goodsname}</p>
                                                    <p style={{ lineHeight: "18px" }}>{i.goodsprice}</p>
                                                </li>
                                                <li style={{ width: "26%" }}>
                                                    <span style={{ fontSize: '22px', marginRight: '3px' }} onClick={() => this.reduce(i.goodsid, i.goodsnum)}>-</span>
                                                    <input goodsid={i.goodsid} type="text" value={i.goodsnum} onChange={(v) => { this.changeCount(i.goodsid, v) }} style={{ width: "34px", fontSize: '16px', textAlign: 'center', background: 'none', border: '0' }} />
                                                    <span style={{ fontSize: '22px' }} onClick={() => this.add(i.goodsid, i.goodsnum)}>+</span>
                                                </li>
                                            </ul>
                                        )
                                    })
                                }
                            </div>
                            <div className="carFoot">
                                <Checkbox style={{ width: '4%', float: 'left', marginLeft: '2%', lineHeight: '50px', }} onClick={this.checkAll} checked={shoppingcar.quan} ></Checkbox>
                                <p onClick={this.delSelect} style={{ width: '28%', lineHeight: '50px', fontSize: '14px', color: "red", marginLeft: '4%' }} ></p>
                                <p style={{ width: '36%', lineHeight: '50px' }}>
                                    :<span style={{ width: '100px' }}> {shoppingcar.total}</span>
                                </p>
                                <p className="xiadan" style={{ backgroundColor: '#5c3715', width: "26%", fontSize: '20px', lineHeight: '50px', textAlign: 'center', color: "#fff", letterSpacing: '2px' }} ></p>
                            </div>
                        </div>
                    </div>
                </div>

         
            
        )
    }
}


